export default {
	data() {
		return {
			historys:[],
			hots:[],
			keyWords: '',
			isShowModel:false,
			showHistory:false
		}
	},
	onShow() {
		this.keyWords = ''
		this.getKeyWords()
	},
	methods: {
		showModel() {
			this.isShowModel = true
		},
		closeModel() {
			this.isShowModel = false
		},
		//获取搜索关键词
		getKeyWords(){
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/Index/search",
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					this.hots = res.data.data.hot_keywords
					if(!res.data.data.q_list||res.data.data.q_list.length==0){
						this.showHistory = false
					}else{
						this.showHistory = true
					}
					this.historys = res.data.data.q_list
				}
			})
		},
		//清除搜索历史记录
		clearHistory(){
			this.setheader()
			uni.request({
				url: this.websiteUrl + "/api/Index/clearSearch",
				method: "POST",
				header: this.header,
				success: (res) => {
					console.log(res.data)
					this.getKeyWords()
					uni.showToast({
						title:'清除成功',
						icon:'success'
					})
					this.closeModel()
				}
			})
		},
		//点击关键词
		setKeyWord(keywords){
			this.keyWords = keywords
			this.getSearchList()
		},
		//跳转显示搜索结果数据列表
		getSearchList() {
			if (this.keyWords == '') {
				this.showToast('请输入搜索关键词')
				return
			}
			uni.navigateTo({
				url:'../searchDetail/searchDetail?keyWords='+this.keyWords
			})
		}
	}
}
